body {
  line-height: 20px;
  font-family: 'Source Sans Pro', sans-serif;
  background-image: url('https://i.imgur.com/lL6qo8n.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left center;
}
.ant-layout-header {
  background: transparent;
}
.ant-layout {
  background: transparent;
}
.ant-drawer-title {
  margin: 0;
  color: #222;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
}
.title {
  -webkit-transition: opacity 0.1s ease;
  transition: opacity 0.1s ease;
}
ul.menu-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  font-size: 16px;
}
ul.menu-list .menu-title-list {
  border-bottom: 1px solid rgba(178, 79, 131, 0.5);
  letter-spacing: 0.05em;
  font-size: 1rem;
  line-height: 28px;
}
ul.menu-list .menu-title-list:last-child {
  border: none;
}
.content {
  background-color: transparent;
  width: 100vw;
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  min-height: 400px;
  padding: 36px;
}
.content .logo-line {
  text-align: center;
  background-color: #fff;
}
.content .logo {
  width: 120px;
  cursor: pointer;
  display: inline-block;
  margin: 0 auto;
}
.content .select-menu {
  background: #fff;
  padding: '30px';
}
.content .heading {
  text-align: center;
  cursor: default;
  letter-spacing: 0.05em;
  font-size: 1.5rem;
  line-height: 32px;
  font-weight: 200;
}
.content .menu {
  text-align: center;
  cursor: pointer;
  letter-spacing: 0.1em;
  font-size: 1rem;
  line-height: 32px;
}
.content .menu-drawer {
  text-align: center;
  cursor: pointer;
  letter-spacing: 0.1em;
  font-size: 1rem;
  line-height: 32px;
}
.content .menu-drawer .menu-type {
  cursor: pointer;
  letter-spacing: 0.1em;
  font-size: 1rem;
  line-height: 32px;
}
.content .menu-drawer .contact-block {
  padding-top: 0;
  padding-left: 17px;
  padding-right: 17px;
  padding-bottom: 17px;
  outline: none;
  box-shadow: inset 0 0 0 1px rgba(128, 128, 128, 0);
  -webkit-transition: box-shadow 0.2s ease-in-out;
  transition: box-shadow 0.2s ease-in-out;
  clear: none;
  position: relative;
  height: auto;
  word-wrap: break-word;
}
.content .menu-drawer .h3-block {
  background-color: #ad8094;
  height: 10px;
  width: 40%;
  margin: 0 auto;
  text-align: center;
  font-weight: 600;
  font-style: normal;
  font-size: 1rem;
  letter-spacing: 0.1em;
  line-height: 1.6em;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0.75);
}
.content .menu-drawer .contact-detail {
  margin-top: 15px;
}
.content .menu-display {
  background-color: #fff;
  padding: 0 30px;
}
.content .menu-display .menu-display-time {
  color: rgba(150, 146, 146, 0.5);
  font-style: normal;
  font-size: 16px;
  letter-spacing: 3px;
  text-transform: uppercase;
  text-align: center;
}
.content .menu-display .menu-display-category {
  font-family: 'Muli', sans-serif;
  text-align: center;
  cursor: default;
  letter-spacing: 0.05em;
  font-size: 1.5rem;
  line-height: 32px;
  font-weight: 200;
}
.content .menu-display .menu-display-name {
  margin-top: 18px;
  padding-top: 18px;
  border-top: 1px solid #121212;
  font-family: 'Muli', sans-serif;
  text-align: left;
  cursor: default;
  letter-spacing: 0.05em;
  font-size: 1.25rem;
  line-height: 32px;
  font-weight: 200;
}
.content .menu-display .menu-display-description {
  padding: 0 18px;
  margin-top: 1rem;
  letter-spacing: 0.05em;
  font-size: 1rem;
  line-height: 28px;
}
.content .menu-display .menu-price {
  text-align: left;
  padding: 0 18px;
  margin-top: 1rem;
  letter-spacing: 0.01em;
  font-size: 0.875rem;
  font-weight: 200;
}
.content .menu-display-last {
  padding-bottom: 1rem;
}
.content .contact-display {
  background-color: #fff;
  padding: 0 30px;
  padding: 1.5rem 0;
}
.content .press-title {
  margin-top: 18px;
  padding: 28px 0;
  font-family: 'Muli', sans-serif;
  text-align: center;
  cursor: default;
  letter-spacing: 0.05em;
  font-size: 1.5rem;
  line-height: 32px;
  font-weight: 200;
  border-bottom: 1px solid #121212;
}
.content .press-headline {
  margin-top: 18px;
  padding-top: 18px;
  border-top: 1px solid #121212;
  font-family: 'Muli', sans-serif;
  text-align: left;
  cursor: default;
  letter-spacing: 0.05em;
  font-size: 1.25rem;
  line-height: 32px;
  font-weight: 200;
}
.content .press-description {
  padding: 0 18px;
  margin-top: 1rem;
  letter-spacing: 0.05em;
  font-size: 1rem;
  line-height: 28px;
}
.content .press-logo {
  margin-bottom: 1rem;
}
.content .carousel {
  padding: 30px;
  background-color: rgba(0, 0, 0, 0.5);
}
.content .carousel .carosel-image {
  width: 320px;
  height: auto;
  margin: 0 auto;
  padding-bottom: 12px;
}
.content .carousel .carosel-image .cover {
  margin-bottom: 1em;
}
.content .carousel .carosel-image-small {
  width: 240px;
  height: auto;
  margin: 0 auto;
  padding-left: 12px;
}
@media only screen and (max-width: 768px) {
  .content .carousel .carosel-image-small {
    width: 350px;
    height: auto;
    padding-bottom: 12px;
  }
}
.content .carousel .carosel-image-large {
  width: 520px;
  height: auto;
  margin: 0 auto;
  padding-bottom: 12px;
}
@media only screen and (max-width: 480px) {
  .content .carousel .carosel-image-large {
    width: 350px;
    height: auto;
  }
}
.content .carousel .carosel-image-insert {
  width: 350px;
  height: auto;
  margin: 0 auto;
  margin-bottom: 12px;
}
.content .carousel .describe {
  margin-top: 30px;
  letter-spacing: 0.05em;
  font-size: 0.875rem;
  line-height: 28px;
}
.footer {
  background: #262626;
}
.footer .footer-block {
  margin: 50px 0;
  padding: 0 30px;
  line-height: 1.25em;
  color: #fff;
  font-weight: 300;
  font-style: normal;
  font-size: 14px;
  letter-spacing: 2px;
}
.footer .copyright {
  color: rgba(150, 146, 146, 0.8);
  text-align: center;
  font-style: normal;
  font-size: 12px;
  letter-spacing: 3px;
  text-transform: uppercase;
}
.text-align-center {
  text-align: center;
}
#map {
  width: 100%;
  height: 400px;
  background-color: grey;
  border: 0;
}
.gutter-example .ant-row > div {
  background: transparent;
  border: 0;
}
.gutter-box {
  background: #00a0e9;
  padding: 5px 0;
}
.thumbnail {
  margin-top: 18px;
  display: inline-block;
  position: relative;
  width: 200px;
  height: 200px;
  overflow: hidden;
  text-align: center;
}
.thumbnail img {
  margin: 0 auto;
  position: absolute;
  left: 50%;
  top: 50%;
  height: 100%;
  width: auto;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.thumbnail img.portrait {
  width: 100%;
  height: auto;
}
